:root {
  --eyedropper-background: linear-gradient(#5c0b0b 0%, #722e2e 100%);
  --eyedropper-gradient-1: radial-gradient(
    circle at 20% 20%,
    #ff9a9e,
    transparent
  );
  --eyedropper-gradient-2: radial-gradient(
    circle at 80% 80%,
    #fad0c4,
    transparent
  );
}
body.light {
  --eyedropper-background: linear-gradient(#f36a6a 0%, #c54747 100%);
  --eyedropper-gradient-1: radial-gradient(
    circle at 20% 20%,
    #e0f7fa,
    transparent
  );
  --eyedropper-gradient-2: radial-gradient(
    circle at 80% 80%,
    #b2dfdb,
    transparent
  );
}

.eyedropper {
  position: relative;
  overflow: hidden; /* Prevents circles from going outside the compartment */
  background: var(--eyedropper-background) !important;
  border-radius: 50px;
}

/* Circles with gradients */
.eyedropper::before,
.eyedropper::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  filter: blur(30px); /* Adds blur effect */
  z-index: 0;
}

/* Circle 1 */
.eyedropper::before {
  width: 300px;
  height: 300px;
  background: var(--eyedropper-gradient-1);
  top: -50px;
  left: -50px;
}

/* Circle 2 */
.eyedropper::after {
  width: 250px;
  height: 250px;
  background: var(--eyedropper-gradient-2);
  bottom: -60px;
  right: -60px;
}

.tool {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  position: relative;
  z-index: 1; /* Ensures the tool content appears above the circles */
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.tool-heading {
  text-align: center;
  line-height: 1;
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tool-heading h2 {
  margin: 0; /* Remove margins to bring them closer */
  padding: 0;
  font-size: 46px;
}

.tool-description {
  text-align: center;
  font-size: 16px;
  line-height: 1.5;
  color: var(--text-color);
  margin: 20px 0;
}

.tool-button {
  position: relative;
  align-self: center;
  height: 50px;
  width: 120px; /* Default width for "Try it out" text */
  background-color: transparent;
  cursor: pointer;
  border: 2px solid var(--text-color);
  border-radius: 30px;
  color: var(--text-color);
  transition: width 0.4s ease, background-color 0.4s ease; /* Smooth transition for width */
  display: flex;
  align-items: center;
  justify-content: center;
}

.tool-button-text {
  z-index: 1;
  font-weight: 800;
  transition: all 0.4s ease-in-out; /* Smooth transition for text change */
}

.tool-button:hover {
  background-color: var(--button-background-color);
  color: var(--button-text-color);
}

/* Bookmark styling */
.tool-button .bookmark {
  position: absolute;
  bottom: -20px;
  right: -15px;
  width: 40px;
  height: 40px;
  background-color: var(--text-color); /* Bookmark background color */
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  cursor: pointer;
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.tool-button:hover .bookmark {
  opacity: 1;
  transform: translateY(5px);
}

.bookmark i {
  color: var(--button-background-color); /* Download icon color */
  font-size: 16px;
}

/* Button grows in width when the text is 'Download Extension' */
.tool-button.grow {
  height: 60px; /* Larger width for 'Download Extension' text */
}




