.dark-mode {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 20px;
}

/* Circle */
.circle {
  position: relative;
  border-radius: 100%;
  width: 8rem;
  height: 8rem;
  background: linear-gradient(40deg, #ff0080, #ff8c00 70%);
  margin: auto;
}

.crescent {
  position: absolute;
  border-radius: 100%;
  right: 0;
  width: 6rem;
  height: 6rem;
  background: var(--compartment-color);
  transform: scale(0);
  transform-origin: top right;
  transition: transform 0.6s cubic-bezier(0.645, 0.045, 0.355, 1);
}

/* Toggle */
.label {
  width: 100%;
  height: 2.8rem;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 100px;
  position: relative;
  margin: 1.8rem 0;
  cursor: pointer;
}

/* Dark Mode State */
.dark-mode.dark .circle {
  background: linear-gradient(40deg, #8983f7, #a3dafb 70%);
}

.dark-mode.dark .crescent {
  transform: scale(1);
  background: var(--compartment-color);
}
