.modal {
  position: relative;
  padding: 50px 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--background-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  overflow: auto;
  transform: scale(0.5); /* Start scale for the transition */
  transition: opacity 0.3s ease, transform 0.3s ease; /* Duration and easing for the transition */
}

.modal.open {
  opacity: 1;
  transform: scale(1); /* Final state for modal */
}

.modal.close {
  opacity: 0;
  transform: scale(0.5); /* Animate back to initial state when closing */
}

.modal-text-container {
  max-width: 30%;
}

.techstack-ul {
  padding: 0;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  margin-bottom: 0;
  margin-top: 21.44px;
}

.tech-list {
  grid-column: span 2;
  display: flex;

  flex-direction: column;
  align-items: center;
  margin: 15px;
  text-align: center;
}

.tech-list:last-child:nth-child(3n - 1) {
  grid-column-end: -2;
}

.tech-list:nth-last-child(2):nth-child(3n + 1) {
  grid-column-end: 4;
}

.tech-icon {
  height: 50px;
  width: 50px;
}

.modal-text{
  font-size: 16px;
  line-height: 1.5;
}

.close-button{
  background: transparent;
  height: 40px;
  width: 40px;
  border: 2.5px solid var(--text-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  margin-bottom: 100px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  color: var(--text-color)
}

.close-button:hover{
  background-color: var(--button-background-color);
  color: var(--button-text-color);
}

.close-button:first-child{
  font-size: 2rem;
}
