:root {
  --bold-color: white;
}

body.light {
  --bold-color: var(--text-color);
}

.about-me {
  display: flex;
  padding: 20px;
  /* align-items: center; */
}
.name-headline {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.name-heading {
  font-size: 40px;
  margin: 0;
}

.headline {
  margin: 0;
  margin-top: 10px;
  font-size: 18px;
  font-weight: 600;
  text-align: center;
}

.image-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
}
.placeholder-circle {
  width: 250px;
  height: 250px;
  border-radius: 100%;
  border: 5px solid var(--text-color);
  background-color: #ccc; /* Placeholder color */
  align-self: center;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.image {
  height: 800px;
  transform: translateY(75px);
  width: 1200px;
}

.leftside {
  display: flex;
  flex-direction: column;
  width: 50%;
  justify-content: space-evenly;
}
.read-more {
  align-self: flex-start;
  height: 50px;
  width: 120px;
  background-color: transparent;
  cursor: pointer;
  border: 2px solid var(--text-color);
  overflow: hidden;
  border-radius: 30px;
  color: var(--text-color);
  transform: translateY(-25px);
  transition: all 0.5s ease-in-out;
}
.button-text {
  z-index: 1;
  font-weight: 800;
}

.read-more:hover {
  background-color: var(--button-background-color);
  color: var(--button-text-color);
}

.text-container {
  display: flex;
  flex-direction: column;
  place-content: center;
  align-items: center;
}

.text {
  align-self: center;
  font-size: 16px;
  line-height: 1.5;
}

.text-bold {
  font-weight: 700;
  color: var(--bold-color);
  transition: all 0.5s ease-in-out;
}
