:root {
  --background-color: #1a1a1a;
  --text-color: #f5f5f5;
  --compartment-color: #111;
  --button-background-color: #ccc;
  --button-text-color: #111;
  --compartment-box-shadow: 0.3em 0.3em 0 0.1em black;
  --compartment-border-color: black;
  --compartment-border-style: solid;

  --dark-mode-grid-row: 1 / 2;
  --dark-mode-grid-column: 4 / 5;

  --links-grid-row: 3 / 4;
  --links-grid-column: 3 / 4;

  --about-me-grid-row: 1 / 3;
  --about-me-grid-column: 1 /4;

  --designers-grid-row: 4 / 5;
  --designers-grid-column: 2 / 5;

  --eyedropper-grid-row: 2 / 4;
  --eyedropper-grid-column: 4 / 5;

  --skills-grid-row: 4 / 5;
  --skills-grid-column: 1 /2;

  --spotify-grid-row: 3 / 4;
  --spotify-grid-column: 1 / 3;
}

body.light {
  --background-color: #e8e8e8;
  --text-color: #222;
  --compartment-color: white;
  --button-background-color: #111;
  --button-text-color: #fff;
  --compartment-box-shadow: 0.3em 0.3em 0 0.1em black;
  --compartment-border-color: black;
  --compartment-border-style: solid;
}

body.about {
  --links-grid-row: 1 / 2;
  --links-grid-column: 4 / 5;

  --dark-mode-grid-row: 3 / 4;
  --dark-mode-grid-column: 3 / 4;
}

body.projects {
  --designers-grid-row: 1 / 2;
  --designers-grid-column: 1 / 4;

  --spotify-grid-row: 2 / 3;
  --spotify-grid-column: 1 / 3;

  --eyedropper-grid-row: 1 / 3;
  --eyedropper-grid-column: 4 / 5;

  --dark-mode-grid-row: 2 / 3;
  --dark-mode-grid-column: 3 / 4;

  --about-me-grid-row: 3 / 5;
  --about-me-grid-column: 1 /4;

  --links-grid-row: 3 / 4;
  --links-grid-column: 4 / 5;

  --skills-grid-row: 4 / 5;
  --skills-grid-column: 4 / 5;
}
p,
button {
  font-family: "Montserrat", sans-serif;
}

body {
  background: var(--background-color);
  color: var(--text-color);
  transition: all 0.5s ease-in-out;
}
.page-wrapper {
  display: flex;
  justify-content: center; /* Horizontally center the grid */
  align-items: center; /* Vertically center the grid */
  height: auto; /* Full height of the viewport */
}

.container {
  display: grid;
  margin-top: 150px;
  margin-bottom: 150px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-template-rows: repeat(auto-fit, minmax(250px, 1fr));
  gap: 50px;
  align-items: center;
  justify-content: center;
  max-width: 1200px; /* Adjust max width as needed */
  width: 100%; /* Optional: ensures container takes up available width */
  max-height: 150vh; /* Ensure it doesn't overflow the viewport */
  box-sizing: border-box;
}

@media (max-width: 768px) {
  .container {
    grid-template-columns: 1fr; /* Stack compartments vertically on smaller screens */
    grid-template-rows: auto; /* Let rows adjust naturally */
  }
}

.compartment {
  border: 1px solid var(--text-color);
  border-radius: 50px;
  box-shadow: var(--compartment-box-shadow);
  border-color: var(--compartment-border-color);
  border-width: 0.4em;
  border-style: var(--compartment-border-style);
  color: var(--text-color);
  box-sizing: border-box;
  height: 100%;
  background: var(--compartment-color);
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}

.about-me {
  grid-column: var(--about-me-grid-column);
  grid-row: var(--about-me-grid-row);
}

.designers {
  grid-column: var(--designers-grid-column);
  grid-row: var(--designers-grid-row);
}

.eyedropper {
  grid-column: var(--eyedropper-grid-column);
  grid-row: var(--eyedropper-grid-row);
}

.dark-mode {
  grid-column: var(--dark-mode-grid-column);
  grid-row: var(--dark-mode-grid-row);
}

.links {
  grid-column: var(--links-grid-column);
  grid-row: var(--links-grid-row);
}
.skills {
  grid-column: var(--skills-grid-column);
  grid-row: var(--skills-grid-row);
}
.spotify {
  grid-column: var(--spotify-grid-column);
  grid-row: var(--spotify-grid-row);
}

.blurred {
  filter: blur(5px);
  pointer-events: none; /* Disable interaction */
  opacity: 0.5; /* Add some transparency to visually indicate it's disabled */
}
