:root {
  --grid-lines: linear-gradient(
      90deg,
      rgba(255, 255, 255, 0.1) 1px,
      transparent 1px
    ),
    linear-gradient(rgba(255, 255, 255, 0.1) 1px, transparent 1px);
}

body.light {
  --grid-lines: linear-gradient(90deg, rgba(0, 0, 0, 0.2) 1px, transparent 1px),
    linear-gradient(rgba(3, 3, 3, 0.2) 1px, transparent 1px);
}

.designers {
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background-color: #0b0b2e; /* Deep space blue */
  border-radius: 40px;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

/* Space-inspired grid lines */
.designers::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: var(--grid-lines);
  background-size: 40px 40px;
  opacity: 0.3;
  z-index: 1;
}

/* Radial shape around the text */

.designers h1 {
  font-size: 2.5rem;
  font-weight: bold;
  position: relative;
  z-index: 2;
  color: var(--text-color);
  letter-spacing: 0.05em;
  text-align: center;
}
