    --transition-speed: 0.3s ease-in-out;
  } */

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: fixed;
  top: 0;
  width: 100%;
  height: 120px; /* Adjusted height for a sleeker look */
  background-color: var(--background-color);
  z-index: 1000;
  padding: 10px 0;
  transition: all 0.5s ease-in-out;
}

.filters {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: transparent;
  cursor: pointer;
  box-shadow: var(--compartment-box-shadow);
  border-color: var(--compartment-border-color);
  border-width: 0.4em;
  border-style: var(--compartment-border-style);
  padding: 5px;
  border-radius: 50px;
  transition: all 0.5s ease-in-out;
}

.filter-item {
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  color: var(--text-color);
  transition: all 0.2s ease-in-out;
}

.filter-item:hover,
.resume-div:hover {
  color: var(--button-text-color);
}

.resume-div:hover {
  background-color: var(--button-background-color);
}

.filter-item.active {
  transform: scale(0.95); /* Add subtle scale effect on click */
  background-color: var(--button-background-color);
  color: var(--button-text-color);
  border-radius: 30px;
}

.resume-div {
  text-decoration: none;
  color: var(--text-color);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 100px;
  box-shadow: var(--compartment-box-shadow);
  border-color: var(--compartment-border-color);
  border-width: 0.4em;
  border-style: var(--compartment-border-style);
  border-radius: 50px;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}

.resume {
  margin: 0px;
  padding: 10px 20px;
  transform: translateY(-3px);
  font-size: 20px;
}

@media (max-width: 768px) {
  /* Responsive adjustments for smaller screens */
  .header {
    height: 70px;
  }

  .filters {
    padding: 8px 20px;
  }

  .filter-item {
    font-size: 14px;
    padding: 8px 15px;
  }
}
/* Header.css */
