:root {
  --spotify-background: linear-gradient(#0b5c19 0%, #2e7234 100%);
  --spotify-gradient-1: radial-gradient(
    circle at 20% 20%,
    #aeff9a,
    transparent
  );
  --spotify-gradient-2: radial-gradient(
    circle at 80% 80%,
    #c4fac8,
    transparent
  );
}
body.light {
  --spotify-background: linear-gradient(#6af38c 0%, #47c566 100%);
  --spotify-gradient-1: radial-gradient(
    circle at 20% 20%,
    #e0f7fa,
    transparent
  );
  --spotify-gradient-2: radial-gradient(
    circle at 80% 80%,
    #b2dfdb,
    transparent
  );
}

.spotify {
  padding: 20px;
  background: var(--spotify-background) !important;
  padding: 0;
  max-height: 260px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
}

/* Circles with gradients */
.spotify::before,
.spotify::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  filter: blur(30px);
  z-index: 0;
}

.spotify::before {
  width: 300px;
  height: 300px;
  background: var(--spotify-gradient-1);
  top: -50px;
  left: -50px;
}

.spotify::after {
  width: 250px;
  height: 250px;
  background: var(--spotify-gradient-2);
  bottom: -60px;
  right: -60px;
}

.glass-card {
  width: 90%;
  height: 80%;
  background-color: rgba(255, 255, 255, 0.25);
  border-radius: 30px;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  color: #fff;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  z-index: 1;
  margin: auto;
  overflow: hidden;
}

.song-container {
  position: relative;
  height: 100%;
  width: 100%;
  /* z-index: 1; */
  display: flex;
  flex-direction: row;
  overflow: hidden;
}

.song-container img {
  height: 200px;
  width: 200px;
}

.details {
  display: grid;
  padding: 15px;
  overflow: hidden;
  max-height: 200px;
}
.song-text {
  position: relative;
  align-self: flex-end;
  justify-self: flex-end;
  display: inline;
  color: var(--text-color);
  bottom: 0px;
}
.music-button {
  position: relative;
  justify-self: flex-start;
  align-self: flex-start;
  background: transparent;
  border: none;
  cursor: pointer;
}

.music-button :first-child {
  height: 30px;
  width: 30px;
  color: var(--text-color);
  transition: color 0.3s ease, transform 0.3s ease;
}

.music-button :first-child:hover {
  transform: scale(1.2); /* Slight zoom effect on hover */
}

.song-name {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 600;
  font-size: 1.5rem;
  margin-top: 10px;
  margin-bottom: -10px;
}

.song-artist {
  font-size: 1rem;
  margin-bottom: 5px;
  font-weight: 600;
}

.glass-card .button {
  background-color: rgba(255, 255, 255, 0.3);
  color: var(--text-color);
  border: none;
  height: 20%;
  width: 7%;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
}

.button-previous {
  left: 0px; /* Align to the left */
}

.button-next {
  right: 0px; /* Align to the right */
}

.glass-card button:disabled {
  background-color: rgba(255, 255, 255, 0.1);
  cursor: not-allowed;
}

.project-link {
  position: relative;
  background-color: var(--text-color);
  border-radius: 50px;
  display: flex;
  flex-direction: row;
  margin-bottom: 0;
  width: 70%;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.project-link-text,
.project-link-button {
  color: var(--background-color);
}
.project-link-button {
  margin-left: 20px;
  background: transparent;
  cursor: pointer;
  height: 35px;
  width: 35px;
  border-radius: 8px;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-link-button :first-child {
  height: 25px;
  width: 25px;
}
